const BASE_URL = "http://localhost:8080/api/movies"; //  API 
const ACTOR_BASE_URL = "http://localhost:8080/api/actors"; //  API 

// 
export const getMovies = async (page = 1, limit = 10) => {
  const response = await fetch(`${BASE_URL}?page=${page}&limit=${limit}`);
  if (!response.ok) throw new Error("Failed to fetch movies");
  return response.json();
};

// 
export const getMovie = async ({ queryKey }) => {
  const [, { id }] = queryKey; //  ID
  const response = await fetch(`${BASE_URL}/${id}`); //  API
  if (!response.ok) throw new Error(`Failed to fetch movie details for ID ${id}`);
  return response.json();
};

// 
export const getMovieImages = async ({ queryKey }) => {
  const [, { id }] = queryKey;
  const response = await fetch(`${BASE_URL}/${id}/images`); //  API
  if (!response.ok) throw new Error(`Failed to fetch images for movie ID ${id}`);
  return response.json();
};

